"use client"
import Sidebar from "@/app/components/ui/Sidebar";
import { useState } from "react"
import Image from "next/image";
import {motion} from 'framer-motion'



export default function Dashboard() {
    const [expand, setExpand] = useState(false);
    const [message, setMessage] = useState([]);
    const [isLoading, setIsLoading] = useState(false);


    return <div className="flex  w-full h-screen bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100">
        <div className="left-0 sticky mr-96">  <Sidebar/>  </div>
        <div className="flex  justify-center ml-40 mt-64"> 
        {message.length == 0 ? (<div >
            <motion.div   initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{
                duration: 0.4,
                scale: { type: "spring", visualDuration: 0.4, bounce: 0.5,  },
            }} className="rounded-full p-2 outline-1 outline-purple-300 border-2 border-purple-200 bg-purple-100 "><Image alt="pic" className="rounded-full" width={60} height={60} src={'/logo.png'}/> </motion.div>
            <motion.p  animate={{ scale: 1.2 }} className="flex justify-center text-3xl text-purple-800 font-bold mt-1.5"> Asong  </motion.p> 
            <p> </p>
             </div>): (<div> 
                </div>)}

                </div>
       
    </div>

}